import {connect} from "react-redux";
import {MyAvatar} from "./MyAvatar";
import {
  loadUserProfile,
  putAuthUserStatus,
  setAuthProfileImage,
} from "../../../redux/mainBasicPage-reducer";
import * as React from "react";
import {setAuthUserProfileStatus} from "../../../redux/auth";

class MyAvatarContainer extends React.Component {
  render() {
    return (
        <MyAvatar {...this.props}/>
    )
  }
}

const mapStateToProps = (state) => {
  return {
    img: state.mainBasicPage.myDescription.img,
    description: state.mainBasicPage.myDescription.description,
    myId: state.auth.userData.userId,
    authoriseStatus: state.auth.userData.authoriseStatus,
    myRealDescription: state.auth.authUserIdProfile.aboutMe,
    myPhoto: state.auth.authUserIdProfile.photos.large,
    myStatus: state.auth.authUserIdProfile.userStatus

    //myPhoto: state.mainBasicPage.userIdProfile.photos.large
  }
}

export default connect(mapStateToProps, {
  setAuthUserProfileStatus,
  putAuthUserStatus,
  setAuthProfileImage,
  loadUserProfile
})(MyAvatarContainer);

